import { Component, OnInit } from '@angular/core';
import { AnalyticsService } from './@core/utils/analytics.service';

import {GlobalDataService,SiteStatistic,SiteCollectionState,Configuration,SiteCollectionDashboard} from "./@core/data-services/globaldata.service"

@Component({
  selector: 'ngx-app',
  template: '<router-outlet></router-outlet>',
  providers: [GlobalDataService]
})
export class AppComponent implements OnInit {
  public globalDataService:GlobalDataService;

  public configuration:Configuration;
  public siteStatistics:SiteStatistic[];


  constructor(private analytics: AnalyticsService,globalDataService:GlobalDataService) {
      console.log("Initialzing app.components")
      this.globalDataService=globalDataService;
  }

  ngOnInit() {
    this.analytics.trackPageViews();

    this.globalDataService.getConfiguration.subscribe((res:Configuration)=>{
      this.configuration=res;
    });
    this.globalDataService.getSiteStatistics.subscribe((res:SiteStatistic[])=>{
      if (res!=null) {
        this.siteStatistics=res;
      }
    });
  }
}
